import React, { Fragment, useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import './app-rack-properties.css'

const AppRackProperties = (props) => {
  // Always-selected state: 'deck' | 'floor'
  const [mountType, setMountType] = useState('deck') // default is deck
  const [tierCount, setTierCount] = useState(1) // default is 1 tier

  useEffect(() => {
    if (props.initialMountType === 'deck' || props.initialMountType === 'floor') {
      setMountType(props.initialMountType)
    }
  }, [props.initialMountType])

  const selectDeck = () => {
    if (mountType !== 'deck') {
      setMountType('deck')
      props.onMountTypeChange?.('deck')
    }
  }

  const selectFloor = () => {
    if (mountType !== 'floor') {
      setMountType('floor')
      props.onMountTypeChange?.('floor')
    }
  }

  const handleTierCountChange = (event) => {
    const count = parseInt(event.target.value)
    setTierCount(count)
  }

  // Generate tier height inputs based on tier count
  const renderTierHeightInputs = () => {
    const tierInputs = []
    for (let i = 1; i <= tierCount; i++) {
      tierInputs.push(
        <div key={i} className="app-rack-properties-tier-height">
          <span className="app-rack-properties-tier-label">Tier {i}</span>
          <div className="app-rack-properties-tier-inputs">
            <input
              type="number"
              id={`TierHeight${i}Input`}
              name={`TierHeight${i}Input`}
              placeholder={`Enter tier ${i} height`}
              className="input-form"
            />
            <span className="app-rack-properties-unit4">ft</span>
            <input
              type="number"
              id={`TierHeight${i}InchesInput`}
              name={`TierHeight${i}InchesInput`}
              placeholder="inches"
              className="input-form"
              style={{ width: '80px', marginLeft: '8px' }}
            />
            <span className="app-rack-properties-unit5">in</span>
          </div>
        </div>
      )
    }
    return tierInputs
  }

  return (
    <div className={`app-rack-properties-container ${props.rootClassName} `}>
      <div className="app-rack-properties-heading">
        <h1 className="heading"> Trade Rack Properties</h1>
        <svg
          id="CloseButton"
          width="32"
          height="32"
          viewBox="0 0 32 32"
          className="app-rack-properties-icon1 button-icon"
          onClick={props.onClose}
        >
          <path
            d="M17.414 16L26 7.414L24.586 6L16 14.586L7.414 6L6 7.414L14.586 16L6 24.586L7.414 26L16 17.414L24.586 26L26 24.586z"
            fill="currentColor"
          ></path>
        </svg>
      </div>

      <div className="app-rack-properties-trade-rack">
        {/* Mount type: one always selected, default deck */}
        <div className="app-rack-properties-mount-type">
          <button
            type="button"
            aria-pressed={mountType === 'deck'}
            className={`app-rack-properties-button1 save-button ${mountType === 'deck' ? 'selected' : ''}`}
            onClick={selectDeck}
          >
            <span className="app-rack-properties-text11">Deck Mounted</span>
          </button>
          <button
            type="button"
            aria-pressed={mountType === 'floor'}
            className={`app-rack-properties-button2 save-button ${mountType === 'floor' ? 'selected' : ''}`}
            onClick={selectFloor}
          >
            <span className="app-rack-properties-text12">Floor Mounted</span>
          </button>
        </div>

        {/* Inputs matching MEP panel structure */}
        <div className="app-rack-properties-inputs">
          <div className="app-rack-properties-rack-length">
            <input
              type="number"
              id="RackLengthInput"
              name="RackLengthInput"
              placeholder="Enter rack length"
              className="input-form"
            />
            <span className="app-rack-properties-unit1">meters</span>
          </div>

          <div className="app-rack-properties-rack-width">
            <input
              type="number"
              id="RackWidthInput"
              name="RackWidthInput"
              placeholder="Enter rack width"
              className="input-form"
            />
            <span className="app-rack-properties-unit2">meters</span>
          </div>

          <div className="app-rack-properties-bay-width">
            <input
              type="number"
              id="BayWidthInput"
              name="BayWidthInput"
              placeholder="Enter bay width"
              className="input-form"
            />
            <span className="app-rack-properties-unit3">meters</span>
          </div>

          <div className="app-rack-properties-tier-count">
            <select 
              id="TierCountSelect" 
              className="input-form"
              value={tierCount}
              onChange={handleTierCountChange}
            >
              <option value="1">1 Tier</option>
              <option value="2">2 Tiers</option>
              <option value="3">3 Tiers</option>
              <option value="4">4 Tiers</option>
              <option value="5">5 Tiers</option>
            </select>
          </div>

          <div className="app-rack-properties-support-type">
            <select id="SupportTypeSelect" className="input-form">
              <option value="Standard">Standard Support</option>
              <option value="Heavy Duty">Heavy Duty Support</option>
              <option value="Light Duty">Light Duty Support</option>
            </select>
          </div>

          {/* Tier Heights Heading */}
          {tierCount > 0 && (
            <div className="app-rack-properties-tier-heights-heading">
              <span className="app-rack-properties-tier-heights-title">Tier Heights</span>
            </div>
          )}

          {/* Dynamic tier height inputs based on tier count */}
          {renderTierHeightInputs()}
        </div>

        {/* Save section matching MEP panels */}
        <div className="app-rack-properties-save">
          <button type="button" className="app-rack-properties-button save-button">
            <span className="app-rack-properties-save-text">Save Configuration</span>
          </button>
        </div>
      </div>
    </div>
  )
}

AppRackProperties.defaultProps = {
  rackLength: undefined,
  numberTiers: undefined,
  unit: undefined,
  deckMounted: undefined,
  floorMounted: undefined,
  saveButton: undefined,
  downloadRvtButton: undefined,
  bayWidth: undefined,
  rackWidth: undefined,
  rootClassName: '',
  numberTiers1: undefined,
  numberTiers2: undefined,
  initialMountType: 'deck', // default deck
  onMountTypeChange: undefined,
}

AppRackProperties.propTypes = {
  rackLength: PropTypes.element,
  numberTiers: PropTypes.element,
  unit: PropTypes.element,
  deckMounted: PropTypes.element,
  floorMounted: PropTypes.element,
  saveButton: PropTypes.element,
  downloadRvtButton: PropTypes.element,
  bayWidth: PropTypes.element,
  rackWidth: PropTypes.element,
  rootClassName: PropTypes.string,
  numberTiers1: PropTypes.element,
  numberTiers2: PropTypes.element,
  initialMountType: PropTypes.oneOf(['deck', 'floor']),
  onMountTypeChange: PropTypes.func,
}

export default AppRackProperties
